.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@media (prefers-reduced-motion: no-preference) {
  .App-header-hidden {
    animation: App-header-hide 2s linear 0s 1  forwards;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-header-show {
    animation: App-header-show 2s linear 0s 1  forwards;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .hide-table {
    animation: App-header-hide 2s linear 0s 1  forwards;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-header-hide {
  /* 0% {
    max-height: 100vh;
    opacity: 100%;
    min-height: 100vh;
  } */
  100% {
    max-height: 1px;
    opacity: 0;
    min-height: 0;
    display: none;
  }
}

@keyframes App-header-show {
  100% {
    max-height: 100vh;
    opacity: 100%;
    min-height: 100vh;
  }
  0% {
    max-height: 1px;
    opacity: 0;
    min-height: 0;
  }
}